<template>

    <div class="app-login">
        <header class="login-header">登录</header>
        <form class="login-main">
            <div class="main-item">
                <label for="name" class="item-left">账号</label>
                <input type="text" id="name" class="item-right" v-model="userName" placeholder="请输入账号">
            </div>
            <div class="main-item">
                <label for="password" class="item-left">密码</label>
                <input type="text" id="password" class="item-right" v-model="userPassword" placeholder="请输入密码">
            </div>
        </form>
        <footer @click="loginSubmit" class="login-footer">登录</footer>
    </div>
</template>

<script>
export default {
    data() {
        return {
            userName: "",
            userPassword: "",
            data: [
                {
                    cart_goods_number: 3,
                    goods_id: 3,
                    goods_name: "honor7x",
                    goods_img: "honor7x.png",
                    goods_price: 1299,
                    shop_id: 4,
                    shop_name: "honor之家"
                },
                {
                    cart_goods_number: 2,
                    goods_id: 4,
                    goods_name: "honor6x",
                    goods_img: "honor6x.png",
                    goods_price: 1199,
                    shop_id: 4,
                    shop_name: "honor之家"
                },
                {
                    cart_goods_number: 1,
                    goods_id: 1,
                    goods_name: "iPhonex",
                    goods_img: "iPhonex.png",
                    goods_price: 8888,
                    shop_id: 1,
                    shop_name: "iPhone之家"
                }
            ],
            data1: [
                {
                    shop_id: 4,
                    shop_name: "honor之家",
                    children: [
                        {
                            cart_goods_number: 3,
                            goods_id: 3,
                            goods_name: "honor7x",
                            goods_img: "honor7x.png",
                            goods_price: 1299
                        },
                        {
                            cart_goods_number: 2,
                            goods_id: 4,
                            goods_name: "honor6x",
                            goods_img: "honor6x.png",
                            goods_price: 1199
                        }
                    ]
                },
                {
                    shop_id: 1,
                    shop_name: "iPhone之家",
                    children: [
                        {
                            cart_goods_number: 1,
                            goods_id: 1,
                            goods_name: "iPhonex",
                            goods_img: "iPhonex.png",
                            goods_price: 8888
                        }
                    ]
                }
            ]
        };
    },
    methods: {
        loginSubmit() {
            axios
                .post("http://localhost:3000/userLogin", {
                    userName: this.userName,
                    userPassword: this.userPassword
                })
                .then(response => {
                    // console.log(response)
                    if (response.data.state == 200) {
                        console.log(response.data.data[0].user_id);
                        let userInfoId = response.data.data[0].user_id;
                        let userInfoName = response.data.data[0].user_name;
                        localStorage.setItem("userInfoId", userInfoId);
                        localStorage.setItem("userInfoName", userInfoName);
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        }
    }
};
</script>
<style scoped lang="scss">
.app-login {
    .login-header {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        color: #fff;
        background-color: #e93b3d;
    }
    .login-main {
        .main-item {
            display: flex;
            .item-left {
                flex: 0 0 50px;
                width: 50px;
                margin-left: 20px;
            }
            .item-right {
                flex: 1;
                margin-right: 20px;
            }
        }
    }
    .login-footer {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        margin: 0 20px;
        color: #fff;
        background-color: #e93b3d;
    }
}
</style>







