<template>
    <div class="app-goods">
        <header class="goods-header">店铺下所有商品</header>
        <main class="goods-main">
            <div v-for="item in goodsData" :key="item.id" class="main-item">
                <router-link :to="{ path: '/goodsDetail', query: {id: item.goods_id} }">
                <div class="item-left">
                    <img v-lazy="IMAGESURL + item.goods_img">
                </div>
                <div class="item-right">
                    <div class="right-top">{{item.goods_name}}</div>
                    <div class="right-middle">{{item.goods_desc}}</div>
                    <div class="right-bottom">{{item.goods_price}}</div>
                </div>
                </router-link>
            </div>
        </main>
    </div>
</template>

<script>
export default {
    data(){
        return{
            goodsData:''
        }
    },
    methods:{
         getGoodsData() {
            axios
                .get(this.URL + "goodsSelectByShopId/" + this.$route.query.id)
                .then(response => {
                    console.log(response.data.data);
                    this.goodsData = response.data.data;
                })
                .catch(error => {
                    console.log(error);
                });
        }
    },
    created(){
        this.getGoodsData()
    }
}
</script>

<style lang="scss" scoped>
.app-goods{
    .goods-header{
        height: 40px;
        text-align: center;
        line-height: 40px;
        border-bottom: 1px solid #ccc;
    }
    .goods-main{
        .main-item{
            display: flex;
            .item-left{
                flex: 0 0 100px;
                width: 100px;
                img{
                    width: 80px;
                    height: 80px;
                }
            }
            .item-right{
                flex: 1;
            }
        }
    }
}
</style>





